.loader {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  width: 100%;
  height: 4px;
  background-color: #d1dde6; }
  .loader__progress {
    width: 50%;
    height: 100%;
    border-top-right-radius: 2px;
    border-bottom-right-radius: 2px;
    background: -webkit-gradient(linear, left top, right top, from(#84a3fd), to(#396afc));
    background: -webkit-linear-gradient(left, #84a3fd 0%, #396afc 100%);
    background: -o-linear-gradient(left, #84a3fd 0%, #396afc 100%);
    background: linear-gradient(to right, #84a3fd 0%, #396afc 100%); }



